<template>
  <div>
    <div v-if="isLoading">
      <v-ons-progress-circular indeterminate></v-ons-progress-circular>
    </div>
    <div v-else>
      <v-ons-toolbar-button @click="shareDialogVisible = true">
        <i class="zmdi zmdi-cloud" v-if="shareCloud"></i>
        <i class="zmdi zmdi-cloud-outline" :class="signinColor" v-else></i>
      </v-ons-toolbar-button>
    </div>

    <v-ons-alert-dialog :visible.sync="shareDialogVisible" cancelable>
      <span slot="title">Cloud</span>
      <template v-if="viewMode === 'success'">
        <ons-list>
          <ons-list-item>
            <label class="center" for="switch1">{{ cloudSwitch ? '' : '' }}</label>
            <div class="right">
              <v-ons-switch input-id="switch1" v-model="cloudSwitch" @click="checkShare"></v-ons-switch>
            </div>
          </ons-list-item>
        </ons-list>
        <p style="padding: 1em;">
          {{ fbAuth.email }}
          <br />
        </p>
        <v-ons-button modifier="large" style="margin-bottom:2em;" @click="signOutAuth"></v-ons-button>
        <v-ons-button modifier="quiet large" @click="removeAuth"></v-ons-button>
      </template>
      <template v-if="viewMode === 'emailVerify'">
        <p>{{ fbAuth.email }} </p>
        <v-ons-list modifier="auth-select" key="send-email">
          <v-ons-list-item modifier="nodivider">
            <v-ons-card modifier="auth-item" @click="mailLinkFlow(fbAuth.email)">
              <div class="content">
                <i class="zmdi zmdi-email" style="color:#1e88e5;"></i>
              </div>
            </v-ons-card>
          </v-ons-list-item>
        </v-ons-list>
        <v-ons-button modifier="quiet"></v-ons-button>
      </template>
      <template v-if="viewMode === 'email'">
        <p class="auth-decription">8</p>
        <v-ons-list modifier="auth-select" key="auth-email">
          <v-ons-list-item modifier="nodivider">
            <div class="center">
              <v-ons-input
                placeholder="email"
                type="email"
                float
                v-model="inputEmail"
                @blur="checkEmail"
              ></v-ons-input>
            </div>
          </v-ons-list-item>
          <v-ons-list-item modifier="nodivider">
            <div class="center">
              <v-ons-input
                placeholder="password"
                float
                type="password"
                v-model="inputPassword"
                @blur="checkPassword"
              ></v-ons-input>
              <p v-if="mailErrorMsg" class="validation">{{ mailErrorMsg }}</p>
            </div>
          </v-ons-list-item>
          <v-ons-list-item modifier="nodivider">
            <v-ons-card modifier="auth-item" @click="selectAuth('email')">
              <div class="content">
                <i class="zmdi zmdi-email" style="color:#1e88e5;"></i>
              </div>
            </v-ons-card>
          </v-ons-list-item>
        </v-ons-list>
        <p
          class="auth-decription"
        ></p>
      </template>
      <template v-if="viewMode === 'init'">
        <p class="auth-decription"></p>
        <v-ons-list modifier="auth-select" key="auth-select">
          <v-ons-list-item modifier="nodivider">
            <v-ons-card modifier="auth-item" @click="selectAuth('google')">
              <div class="content">
                <i class="zmdi zmdi-google" style="color:#dc5246;"></i>Google
              </div>
            </v-ons-card>
          </v-ons-list-item>
          <!--
          <v-ons-list-item modifier="nodivider">
            <v-ons-card modifier="auth-item" @click="selectAuth('facebook')">
              <div class="content">
                <i class="zmdi zmdi-facebook" style="color:#1774eb;"></i>Facebook
              </div>
            </v-ons-card>
          </v-ons-list-item>
          <v-ons-list-item modifier="nodivider">
            <v-ons-card modifier="auth-item" @click="selectAuth('twitter')">
              <div class="content">
                <i class="zmdi zmdi-twitter" style="color:#1d9dec;"></i>Twitter
              </div>
            </v-ons-card>
          </v-ons-list-item>
          -->
          <v-ons-list-item modifier="nodivider">
            <v-ons-card modifier="auth-item" @click="emailAuth">
              <div class="content">
                <i class="zmdi zmdi-email" style="color:#1e88e5;"></i>
              </div>
            </v-ons-card>
          </v-ons-list-item>
        </v-ons-list>
      </template>
      <template slot="footer">
        <v-ons-alert-dialog-button @click="emailAuthCancel">Cancel</v-ons-alert-dialog-button>
      </template>
    </v-ons-alert-dialog>
  </div>
</template>

<style>
.auth-decription {
  font-size: 70%;
  line-height: 1.4;
}

.button--sendcheck:not(.button--material) {
  font-size: 100%;
}

.list--auth-select .list-item {
  padding-left: 0;
}

.list--auth-select .list-item .list-item__center {
  padding: 0;
}

.card--auth-item {
  width: 100%;
  margin: 2px 5px;
  padding: 0.5em 1em;
}

.list--auth-select .list-item i {
  width: 2em;
}

.validation {
  color: #ff5722;
  font-size: 70%;
  line-height: 1.4;
}

.auth-signin {
  color: #1e88e5;
}

.auth-signout {
  color: #ccc;
}
</style>

<script>
import firebase from "firebase";

export default {
  data() {
    return {
      shareDialogVisible: false,
      inputEmail: "",
      inputPassword: "",
      mailErrorMsg: "",
      isSelectEmailAuth: false,
      cloudSwitch: false,
      isLoading: ""
    };
  },
  computed: {
    fbAuth() {
      return this.$store.state.fbAuth;
    },
    shareCloud() {
      return this.$store.state.memoData.shareCloud;
    },
    signinColor() {
      return this.fbAuth.uid ? "auth-signin" : "auth-signout";
    },
    viewMode() {
      if (this.fbAuth.emailVerified === true) {
        return "success";
      } else if (this.fbAuth.email !== "") {
        return "emailVerify";
      } else if (this.isSelectEmailAuth === true) {
        return "email";
      } else {
        return "init";
      }
    }
  },
  mounted() {
    this.cloudSwitch = this.shareCloud;
  },
  methods: {
    selectAuth(target) {
      if (target === "email") {
        if (this.inputEmail === "" || this.inputPassword === "") {
          this.mailErrorMsg = "";
        }
        if (this.mailErrorMsg !== "") {
          return;
        }

        this.sendMailCheck();
      } else if (target === "google") {
        const provider = new firebase.auth.GoogleAuthProvider();
        firebase
          .auth()
          .signInWithPopup(provider)
          .then(() => {
            this.$ons.notification.toast("Google", {
              timeout: 2000
            });
          })
          .catch(error => {
            alert(error.message);
          });
      }
    },
    emailAuth() {
      this.isSelectEmailAuth = true;
    },
    emailAuthCancel() {
      if (this.isSelectEmailAuth) {
        this.isSelectEmailAuth = false;
        this.mailErrorMsg = "";
      } else {
        this.shareDialogVisible = false;
      }
    },
    checkEmail() {
      var pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,100}))$/;
      if (this.inputPassword !== "" && !pattern.test(this.inputEmail)) {
        this.mailErrorMsg = "";
      } else {
        this.mailErrorMsg = "";
      }
    },
    checkPassword() {
      var pattern = /^[a-zA-Z0-9!-/:-@[-`{-~]{8,100}$/;
      if (this.inputPassword !== "" && !pattern.test(this.inputPassword)) {
        this.mailErrorMsg =
          "8";
      } else {
        this.mailErrorMsg = "";
      }
    },
    sendMailCheck() {
      //
      firebase
        .auth()
        .fetchSignInMethodsForEmail(this.inputEmail)
        .then(providers => {
          if (
            providers.findIndex(
              p =>
                p ===
                firebase.auth.EmailAuthProvider.EMAIL_PASSWORD_SIGN_IN_METHOD
            ) !== -1
          ) {
            //
            firebase
              .auth()
              .signInWithEmailAndPassword(this.inputEmail, this.inputPassword)
              .then(result => {
                //
                if (result.user.emailVerified) {
                  //
                  this.$ons.notification.toast(
                    "",
                    {
                      timeout: 2000
                    }
                  );
                  console.log("user login success.");
                } else {
                  //
                  this.mailLinkFlow(this.inputEmail);
                }
              })
              .catch(error => {
                var errorMessage =
                  error.message ===
                  "The email address is already in use by another account."
                    ? ""
                    : " error: " + error.message;
                this.$ons.notification.toast(
                  "" + errorMessage,
                  { timeout: 2000 }
                );
                console.log(error.message);
              })
              .then(() => {});
          } else {
            //
            firebase
              .auth()
              .createUserWithEmailAndPassword(
                this.inputEmail,
                this.inputPassword
              )
              .then(() => {
                //
                this.mailLinkFlow(this.inputEmail);
              })
              .catch(error => {
                var errorMessage =
                  error.message ===
                  "The email address is already in use by another account."
                    ? ""
                    : " error: " + error.message;
                this.$ons.notification.toast(
                  "" + errorMessage,
                  { timeout: 2000 }
                );
                console.log(error.message);
              });
          }
        })
        .catch(error => {
          this.$ons.notification.toast(
            "error: " + error.message,
            { timeout: 2000 }
          );
          console.log(error.message);
        });
    },
    mailLinkFlow(mail) {
      this.$ons.notification
        .confirm(
          "",
          { title: "", cancelable: true }
        )
        .then(response => {
          if (response === 1) {
            //
            var actionCodeSettings = {
              url:
                process.env.NODE_ENV === "development"
                  ? "http://localhost:8080"
                  : "https://mndkproject.github.io/local_memo/",
              handleCodeInApp: true
            };
            firebase
              .auth()
              .currentUser.sendEmailVerification(actionCodeSettings)
              .then(() => {
                this.$ons.notification.toast(
                  mail +
                    "",
                  { timeout: 2000 }
                );
              })
              .catch(error => {
                this.$ons.notification.toast(
                  "error: " + error.message,
                  { timeout: 2000 }
                );
                console.log(error.message);
              });
          } else {
            this.$ons.notification.toast(
              "",
              { timeout: 2000 }
            );
          }
        });
    },
    signOutAuth() {
      this.$ons.notification
        .confirm(
          "",
          {
            title: "",
            cancelable: true
          }
        )
        .then(response => {
          if (response === 1) {
            this.$store.dispatch("snapshotCheck", "stop");
            firebase
              .auth()
              .signOut()
              .then(() => {
                this.$ons.notification.alert("", {
                  title: "",
                  cancelable: true
                });
                this.shareDialogVisible = false;
              })
              .catch(error => {
                this.$ons.notification.alert(
                  "",
                  {
                    title: "",
                    cancelable: true
                  }
                );
                console.log(` (${error})`);
              });
          }
        });
    },
    removeAuth() {
      this.$ons.notification
        .confirm(
          "",
          {
            title: "",
            cancelable: true
          }
        )
        .then(response => {
          if (response === 1) {
            this.$store.dispatch("removeAuthCheck").then(() => {
              this.$ons.notification.alert("", {
                title: "",
                cancelable: true
              });
              this.shareDialogVisible = false;
            });
          }
        });
    },
    checkShare() {
      if (!this.shareCloud) {
        this.$ons.notification
          .confirm(
            "",
            {
              title: "",
              cancelable: true
            }
          )
          .then(response => {
            if (response === 1) {
              this.$store.dispatch("shareCloudCheck", true);
              //
              this.$store.dispatch("snapshotCheck", "start");
              this.$ons.notification.alert("", {
                title: "",
                cancelable: true
              });
              this.shareDialogVisible = false;
            } else {
              this.cloudSwitch = false;
            }
          });
      } else {
        this.$ons.notification
          .confirm(
            "",
            {
              title: "",
              cancelable: true
            }
          )
          .then(response => {
            if (response === 1) {
              this.$store.dispatch("shareCloudCheck", false);
              this.$store.dispatch("snapshotCheck", "stop");
              this.$ons.notification.alert("", {
                title: "",
                cancelable: true
              });
              this.shareDialogVisible = false;
            } else {
              this.cloudSwitch = true;
            }
          });
      }
    }
  }
};
</script>